.header {
  background: #08111C;
  ul {
    display: flex;
    justify-content: space-between;
    gap: 16px;
    li {
      font-size: 12px;
      line-height: 130%;
      font-weight: 600;
      text-transform: uppercase;
      color: #626D7B;
    }
    li a {
      &:hover {
        color: #FFB800;
      }
    }
  }
}
.header-top {
  padding: 15px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 32px;
  }
  &__right {
    display: flex;
    align-items: start;
    gap: 32px;
  }
  &__logo {
    width: 118px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    font-weight: 600;
  }
  &__socials {
    &-text {
      font-size: 14px;
      line-height: 130%;
      font-weight: 300;
      color: #626D7B;
    }
    &-group {
      display: flex;
      gap: 5px;
      margin-top: 5px;
    }
  }
  &__social {
    &:hover {
      opacity: 0.7;
    }
  }
  &__group {
    display: flex;
    gap: 32px;
  }
  &__email,
  &__phone {
    span {
      display: block;
      text-align: right;
      font-size: 14px;
      line-height: 130%;
      font-weight: 300;
      color: #626D7B;
    }
    a {
      display: block;
     font-size: 20px;
      line-height: 130%;
      margin-top: 10px;
    }
    a:hover {
      color: #FFB800;
    }
  }
  &__email {
    a {
      letter-spacing: 0.2px;
    }
  }
  &__phone {
    a {
      font-weight: 700;
    }
  }
  &__btn {
    background: #0C1724;
    border: 1px solid #626D7B;
    border-radius: 7px;
    padding: 14px 32px;
    font-size: 12px;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
    color: #fff;
    &:hover {
      background: #626D7B;
    }
  }
}
.header-bottom {
  &__inner {
    border-top: 1px solid rgba(56, 66, 79, 0.5);
    border-bottom: 1px solid rgba(56, 66, 79, 0.5);
    padding: 15px 0;
  }
}
.burger {
  display: none;
  align-items: center;
  cursor: pointer;
  &-text {
    font-size: 12px;
    line-height: 130%;
    font-weight: 500;
    color: #626D7B;
  }
  &-group {
    position: relative;
    width: 30px;
    height: 17px;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background: #FFB800;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
  }
}
.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(8, 17, 28, 0.9);
  padding: 30px 0 50px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  overflow-y: auto;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  &-close {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 15px;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .header-top__left {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 5px;
  }
  ul {
    flex-direction: column;
    gap: 40px;
    margin-top: 40px;
    li {
      color: #FFFFFF;
    }
  }
  .header-top__group {
    margin-top: 40px;
  }
  .header-top__email,
  .header-top__phone {
    span {
      text-align: start;
      color: #FFFFFF;
    }
  }
  .header-top__socials {
    margin-top: 16px;
  }
}

@media screen and (max-width: 1150px) {
  .header-top {
    &__inner {
      gap: 16px;
    }
    &__right {
      gap: 16px;
    }
    &__left {
      gap: 16px;
    }
    &__group {
      gap: 16px;
    }
    &__email,
    &__phone {
      a {
        font-size: 16px;
      }
    }
    &__btn {
      padding: 14px 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    &__inner {
      position: relative;
      align-items: flex-end;
    }
    &__text {
      font-size: 12px;
    }
    &__socials {
      display: none;
    }
    &__group {
      flex-shrink: 0;
    }
    &__email,
    &__phone {
      a {
        font-size: 14px;
      }
    }
  }
  .header-bottom {
    display: none;
  }
  .burger {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
  }
  .menu {
    .header-top__text {
      font-size: 14px;
    }
    .header-top__socials {
      display: block;
    }
    .header-top__email,
    .header-top__phone {
      a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header-top {
    &__left {
      align-items: flex-end;
    }
    &__right {
      flex-shrink: 0;
      align-items: flex-end;
    }
    &__logo {
      width: 90px;
    }
    &__group {
      flex-direction: column;
      gap: 7px;
    }
    &__email,
    &__phone {
      span {
        display: none;
      }
      a {
        font-size: 12px;
        margin-top: 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__right {
      width: 100%;
      justify-content: space-between;
      align-items: center;
    }
    &__group {
      flex-direction: row;
      gap: 16px;
    }
    &__email,
    &__phone {
      span {
        display: block;
      }
      a {
        margin-top: 8px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .header-top {
    &__right {
      flex-direction: column;
    }
    &__logo {
      width: 118px;
    }
    &__text {
      font-size: 14px;
    }
    &__btn {
      width: 100%;
      order: -1;
    }
    &__group {
      width: 100%;
      justify-content: space-between;
    }
    &__email,
    &__phone {
      span {
        text-align: start;
      }
      a {
        font-size: 14px;
      }
    }
  }
}