h2 {
  font-size: 32px;
  line-height: 105%;
  font-weight: 600;
  color: #626D7B;
}
.btn-yellow {
  background: rgb(255, 184, 0);
  border: 1px solid rgb(98, 109, 123);
  border-radius: 7px;
  &:hover {
    background: rgb(215, 159, 13);
  }
  span {
    line-height: 130%;
    font-weight: 600;
    color: rgb(8, 17, 28);
  }
}
.whatsapp {
  position: fixed;
  z-index: 5;
  right: 15px;
  bottom: 100px;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #27CB3E;
  border-radius: 50%;
  img {
    max-width: 28px;
  }
}