.main {
  background: #08111C;
  &__wrap {
    background-image: url("../img/main-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 315px 35px 70px;
  }
  h1 {
    font-size: 48px;
    line-height: 100%;
  }
  p {
    font-size: 32px;
    line-height: 130%;
    margin-top: 40px;
    br {
      display: none;
    }
  }
  &__btn {
    margin-top: 40px;
    span {
      font-size: 16px;
      padding: 18px 25px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 150px / 4));
    gap: 50px;
    background: #09111C;
    padding: 35px;
  }
  &__item {
    font-size: 16px;
    line-height: 130%;
    color: #626D7B;
    strong {
      display: block;
      font-weight: 700;
      color: #fff;
      margin-bottom: 5px;
    }
  }
}
.projects {
  padding: 100px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 48px 40px;
    margin-top: 30px;
  }
}
.project-card {
  display: flex;
  flex-direction: column;
  &2 {
    justify-content: space-between;
    align-items: center;
    text-align: center;
    border: 1px solid rgb(98, 109, 123);
    border-radius: 7px;
    padding: 22px 22px 0 22px;
    .project-card__desc {
      margin-top: 0;
    }
    .project-card__img2 {
      max-width: 250px;
      margin: 16px auto 0;
      img {
        width: 100%;
      }
    }
    .project-card__text {
      max-width: 250px;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 50%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    margin-top: 28px;
  }
  &__title {
    font-size: 20px;
    line-height: 130%;
    font-weight: 700;
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    color: #626D7B;
    margin-top: 10px;
    margin-bottom: auto;
  }
  &__price {
    font-size: 25px;
    line-height: 130%;
    font-weight: 700;
    color: #626D7B;
    margin-top: 10px;
  }
  &__btn {
    margin: 16px auto 0;
    span {
      font-size: 12px;
      padding: 14px 32px;
    }
  }
}
.about {
  padding: 100px 0 200px;
  &__wrap {
    display: flex;
    gap: 40px;
    margin-top: 30px;
  }
  &__text {
    max-width: 360px;
    p {
      font-size: 24px;
      line-height: 160%;
      strong {
        font-weight: 700;
        color: rgb(255, 204, 0);
      }
    }
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    li {
      display: flex;
      gap: 12px;
      border-top: 1px solid #626D7B;
      padding: 35px 0;
      &:last-child {
        border-bottom: 1px solid #626D7B;
      }
      i {
        flex-shrink: 0;
        width: 19px;
      }
      i img {
        width: 100%;
        margin-top: 5px;
      }
      p {
        font-size: 24px;
        line-height: 130%;
      }
      p strong {
        font-weight: 700;
      }
    }
  }
}
.connection {
  background-image: url("../img/connection-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 70px 0;
  .form {
    position: relative;
    max-width: 700px;
    background: rgba(12, 23, 36, 0.85);
    border-radius: 4px;
    padding: 50px;
    &-title {
      font-size: 48px;
      line-height: 105%;
      font-weight: 700;
    }
    &-text {
      font-size: 24px;
      line-height: 130%;
      margin-top: 24px;
    }
    &_block {
      max-width: 540px;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 30px 15px;
      margin-top: 34px;
    }
    &-group {
      label {
        display: block;
        font-size: 14px;
        line-height: 130%;
        font-weight: 500;
        margin-bottom: 3px;
      }
      input {
        width: 100%;
        height: 50px;
        background: #fff;
        border-radius: 12px;
        padding: 0 15px;
        font-size: 16px;
        line-height: 130%;
        color: #000;
        &::placeholder {
          color: #7A7A7A;
        }
      }
    }
    &-group--100 {
      grid-column: 1/3;
    }
    &-file {
      position: relative;
      border: 1px dashed #626D7B;
      border-radius: 12px;
      padding: 15px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
      }
      label {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background: #FFCC00;
        border-radius: 12px;
        padding: 3px 5px;
        color: #000;
      }
      &__text {
        font-size: 14px;
        line-height: 130%;
        margin-top: 5px;
      }
    }
    &-btn {
      span {
        font-size: 16px;
        padding: 18px 70px;
      }
    }
    &__img {
      position: absolute;
      left: calc(100% - 10px);
      bottom: -70px;
      width: 425px;
      img {
        width: 100%;
      }
    }
  }
  &__img {
    display: none;
  }
}
.examples {
  background: #091A2F;
  padding: 100px 0;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .swiper-pagination {
    position: static;
    width: auto;
    margin-top: 40px;
    &-bullet {
      width: 6px;
      height: 6px;
      background: #fff;
      opacity: 0.2;
    }
    &-bullet-active {
      background: #43B81F;
      opacity: 1;
    }
  }
}
.example-card {
  display: flex;
  align-items: start;
  gap: 30px;
  &__img {
    width: 48%;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: 52%;
    max-width: 450px;
  }
  &__year {
    font-size: 14px;
    line-height: 130%;
    font-weight: 600;
    color: #626D7B;
  }
  &__title {
    font-size: 30px;
    line-height: 130%;
    font-weight: 600;
    margin-top: 22px;
    margin-bottom: 22px;
  }
  &__text {
    p {
      font-size: 16px;
      line-height: 130%;
      color: #626D7B;
    }
  }
  ul {
    li {
      position: relative;
      font-size: 16px;
      line-height: 130%;
      color: #626D7B;
      padding-left: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 5px;
        height: 5px;
        background: #626D7B;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    background: transparent;
    border: 1px solid rgb(98, 109, 123);
    border-radius: 7px;
    margin-top: 40px;
    &:hover {
      background: rgb(98, 109, 123);
    }
    span {
      font-size: 12px;
      line-height: 130%;
      font-weight: 600;
      color: #FFCC00;
      padding: 12px 35px;
    }
  }
}
.stages {
  background: #091A2F;
  padding: 100px 0;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 70px;
  }
  &__title {
    font-size: 42px;
    line-height: 130%;
    font-weight: 600;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 80px / 3));
    gap: 40px;
  }
  &__item {
    &-number {
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgb(255, 204, 0);
      border: 2px solid rgb(34, 34, 34);
      border-radius: 50%;
      font-size: 16px;
      line-height: 105%;
      font-weight: 600;
      color: rgb(34, 34, 34);
    }
    &-title {
      font-size: 20px;
      line-height: 130%;
      font-weight: 600;
      margin-top: 16px;
    }
    &-text {
      font-size: 15px;
      line-height: 130%;
      color: rgb(98, 109, 123);
      margin-top: 12px;
    }
  }
  &__btn {
    span {
      font-size: 16px;
      padding: 10px 80px;
    }
  }
}
.connection2 {
  background: #08111C;
  padding: 65px 0 95px;
  &__inner {
    display: flex;
    gap: 120px;
  }
  &__left {
    width: 50%;
    max-width: 480px;
  }
  &__right {
    width: 50%;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__logo {
    max-width: 260px;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    font-weight: 600;
    color: #626D7B;
  }
  &__title {
    font-size: 40px;
    line-height: 130%;
    font-weight: 700;
    margin-top: 40px;
  }
  &__group2 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 40px;
    margin-top: 40px;
  }
  &__email,
  &__phone,
  &__location,
  &__time {
    span {
      display: block;
      font-size: 14px;
      line-height: 130%;
      font-weight: 300;
      color: #626D7B;
    }
    a,
    p {
      display: block;
      font-size: 20px;
      line-height: 130%;
      margin-top: 10px;
    }
    p {
      font-size: 16px;
    }
    a:hover {
      color: #FFB800;
    }
  }
  &__email {
    a {
      letter-spacing: 0.2px;
    }
  }
  &__phone {
    a {
      font-weight: 700;
    }
  }
  &__socials {
    &-text {
      font-size: 14px;
      line-height: 130%;
      font-weight: 300;
      color: #626D7B;
    }
    &-group {
      display: flex;
      gap: 5px;
      margin-top: 5px;
    }
  }
  &__social {
    &:hover {
      opacity: 0.7;
    }
  }
  &__btn {
    background: #0C1724;
    border: 1px solid #626D7B;
    border-radius: 7px;
    padding: 14px 32px;
    font-size: 12px;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
    color: #fff;
    &:hover {
      background: #626D7B;
    }
  }
  .map{
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
}

@media screen and (max-width: 1150px) {
  .main {
    &__wrap {
      padding-top: 250px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .connection {
    .form {
      &__img {
        width: 350px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__wrap {
      padding: 200px 25px 70px;
    }
    h1 {
      font-size: 32px;
    }
    p {
      font-size: 20px;
      margin-top: 24px;
    }
    &__btn {
      margin-top: 24px;
    }
    &__items {
      grid-template-columns: repeat(4, calc(25% - 72px / 4));
      gap: 24px;
      padding: 35px 25px;
    }
    &__item {
      font-size: 14px;
    }
  }
  .projects {
    padding: 60px 0 30px;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 40px / 2));
    }
  }
  .about {
    padding: 30px 0 60px;
    &__text {
      max-width: 340px;
      p {
        font-size: 20px;
      }
    }
    ul {
      li {
        p {
          font-size: 20px;
        }
      }
    }
  }
  .connection {
    padding: 60px 0;
    .form {
      max-width: 550px;
      &-title {
        font-size: 32px;
      }
      &-text {
        font-size: 20px;
      }
      &__img {
        left: calc(100% - 40px);
        bottom: -60px;
        width: 280px;
      }
    }
  }
  .examples {
    padding: 60px 0;
  }
  .example-card {
    &__title {
      font-size: 20px;
      margin: 15px 0;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    ul {
      li {
        font-size: 14px;
        &:after {
          top: 6px;
          width: 4px;
          height: 4px;
        }
      }
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .stages {
    padding: 60px 0;
    &__title {
      font-size: 32px;
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
      gap: 30px;
    }
    &__item {
      &-title {
        font-size: 18px;
      }
    }
  }
  .connection2 {
    padding: 60px 0;
    &__inner {
      gap: 20px;
    }
    &__group {
      flex-direction: column;
      align-items: start;
      gap: 5px;
    }
    &__title {
      font-size: 32px;
      margin-top: 20px;
    }
    &__group2 {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 20px;
    }
    &__phone,
    &__email {
      a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__wrap {
      padding: 80px 15px 40px;
    }
    &__items {
      grid-template-columns: repeat(4, calc(25% - 48px / 4));
      gap: 16px;
      padding: 35px 15px;
    }
  }
  .about {
    &__wrap {
      flex-direction: column;
      gap: 20px;
    }
    &__text {
      max-width: 100%;
      p {
        font-size: 16px;
      }
    }
    ul {
      li {
        padding: 20px 0;
        p {
          font-size: 16px;
        }
      }
    }
  }
  .connection {
    padding-bottom: 0;
    .form {
      margin: 0 auto;
      &__img {
        display: none;
      }
    }
    &__img {
      position: relative;
      z-index: 1;
      display: block;
      max-width: 300px;
      margin: -40px -15px 0 auto;
      img {
        width: 100%;
      }
    }
  }
  .examples {
    &__inner {
      max-width: 360px;
      margin: 0 auto;
    }
    .swiper-pagination {
      margin-top: 30px;
    }
  }
  .example-card {
    flex-direction: column;
    gap: 15px;
    &__img {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
  .stages {
    &__inner {
      gap: 40px;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
    }
  }
  .connection2 {
    padding-bottom: 0;
    &__inner {
      flex-direction: column;
      gap: 20px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    .map {
      height: 500px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    p {
      br {
        display: block;
      }
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 46px / 2));
      gap: 46px;
    }
  }
  .projects {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .connection {
    padding-top: 30px;
    .form {
      padding: 30px 30px 40px;
    }
  }
  .connection2 {
    &__left {
      max-width: 100%;
    }
    &__group2 {
      grid-template-columns: 100%;
    }
    &__btn {
      max-width: 280px;
    }
  }
}

@media screen and (max-width: 500px) {
  .connection {
    .form {
      &_block {
        grid-template-columns: 100%;
        gap: 15px;
      }
      &-group--100 {
        grid-column: unset;
      }
      &-btn {
        width: 100%;
        span {
          padding: 18px;
        }
      }
    }
  }
  .stages {
    &__items {
      grid-template-columns: 100%;
    }
    &__btn {
      span {
        padding: 10px 40px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__items {
      grid-template-columns: 100%;
    }
  }
}