.footer {
  background: #0C1724;
  padding: 40px 0;
  &__text {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    gap: 15px 30px;
    p {
      font-size: 14px;
      line-height: 130%;
      color: #626D7B;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 20px 0;
    &__text {
      flex-direction: column;
    }
  }
}